import {
		mapState
	} from 'vuex';
import md5 from '../../kayida/common/md5.js'
import easyLoadimage from '@/components/easy-loadimage/easy-loadimage.vue'
const MockData = require('@/static/easy-loadimage/mock-data.json')

export default {
	components: {easyLoadimage},
	computed: {
	            ...mapState(['userInfo','is_bind'])  
	        },
	data() {
		return {
			canvasId: 'default_PosterCanvasId',
			id:'',
			pro_list:'',
			share:0,
			pic:'',
			popup:0,
			scrollTop:0,
			list:MockData
		};
	},
	onPageScroll({scrollTop}) {
		// 传入scrollTop值并触发所有easy-loadimage组件下的滚动监听事件
		this.scrollTop = scrollTop;
	},
	onLoad(option) {
		this.id = option.id
		let data = {
			id:this.id,
			user_id: this.userInfo.user_id,
			token: this.userInfo.token,
			sign: md5(this.userInfo.user_id+this.userInfo.token)
		}
		this.request.httpRequest('api/poster/poster',data,'GET').then(res =>{
			if(res.data.code === 1000){
				this.pro_list = res.data.retval
				this.pic = res.data.retval.poster
			}
		});
	},
	methods: {
		moveHandle() {
			return
		},
		downPromo() {
			this.popup = 1
		},
		canceldown() {
			this.popup = 0
		},
		downPic() {
			uni.downloadFile({
				url: this.pro_list.poster,
				success: (res) =>{
					if (res.statusCode === 200){
						uni.saveImageToPhotosAlbum({
							filePath: res.tempFilePath,
							success: function() {
								uni.showToast({
									title: "保存成功",
									icon: "none"
								});
							},
							fail: function() {
								uni.showToast({
									title: "保存失败，请稍后重试",
									icon: "none"
								});
							}
						});
					}
				}
			})
			this.popup = 0
		},
		shareWeixin() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSceneSession",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
						icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareFriends() {
			uni.share({
			    provider: "weixin",
			    scene: "WXSenceTimeline",
			    type: 2,
			    imageUrl: this.pic,
			    success: function (res) {
			        uni.showToast({
			        	title:"分享成功",
			        	icon:"none"
			        });
			    },
			    fail: function (err) {
			        uni.showToast({
			        	title:"分享失败",
			        	icon:"none"
			        });
			    }
			});
			this.share = 0
		},
		shareCancel() {
			this.share = 0
		},
		shareDetail() {
				this.share = 1
		},
	}
}
